import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform } from 'ionic-angular';
import { ViewChild } from '@angular/core';
 import { ImageViewerController } from 'ionic-img-viewer';
/**
 * Generated class for the QapstripsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-qapstrips',
  templateUrl: 'qapstrips.html',
})
export class QapstripsPage {





 @ViewChild('mySlider') mySlider: any;
_imageViewerCtrl: ImageViewerController;
  constructor(public navCtrl: NavController, public navParams: NavParams, imageViewerCtrl: ImageViewerController, public platform: Platform) {
  this._imageViewerCtrl = imageViewerCtrl;
  
    let backAction =  platform.registerBackButtonAction(() => {
        console.log("contact");
        this.navCtrl.pop();
        backAction();
      },2)
  
  }
  
   presentImage(myImage) {
    const imageViewer = this._imageViewerCtrl.create(myImage);
    imageViewer.present();
  }
  
  elenco =  [
             {
               "img": "assets/img/qaprofil/qap50/1.png",
               "posicao": "Qap 50 Strips",
               "nome": "QAQ50"
               
             },
             {
               "img": "assets/img/qaprofil/qap50/2.png",
                 "posicao": "Qap 50 Strips",
               "nome": "QAQ50"
               
             },
             {
               "img": "assets/img/qaprofil/full_image/qap50-1.jpg",
               "posicao": "Qap 50 Strips",
               "nome": "QAQ50"
             } 
             
           ]
           
           
           slideNext(){
       this.mySlider.slideNext();
     }
 
     slidePrev(){
       this.mySlider.slidePrev();
     }



  ionViewDidLoad() {
    console.log('ionViewDidLoad QapstripsPage');
  }
  
  

}



